<template lang="html">

  <div class="usluga">



  <div class="headUsluga">

<router-link class="navbar-brand" to="/ksusluge">Back </router-link>
  <h3>Executive Search</h3>
  <p>Executive Search je usluga kojom korišćenjem direktnog pristupa i pregleda bogate baze kandidata pronalazimo stručne kandidate koji svojim kompetencijama  odgovaraju potrebama Klijenata i uslovima za određenu poziciju.</p>
  <h5>Potrebna Vam je ova usluga? Popunite polja ispod:</h5>

  </div>
  <div class="card-body">

       <!-- @submit.prevent="registerUser"  -->
      <div class="form-group">
        <label for="companyName">Naziv Kompanije</label>
        <input
          class="form-control"
          type="text"
          placeholder=""
          name="companyName"
          v-model="companyName"
          id="companyName"
          >
      </div>
      <div class="form-group">
        <label for="contactName">Ime i Prezime Kontakt Osobe</label>
        <input
          class="form-control"
          type="text"
          placeholder=""
          name="contactName"
          v-model="contactName"
          id="contactName"
          >
      </div>
      <div class="form-group">
        <label for="contactNumber">Kontakt telefon</label>
        <input
          class="form-control"
          type="text"
          placeholder=""
          name="contactNumber"
          v-model="contactNumber"
          id="contactNumber"
          >
      </div>
      <div class="form-group">
        <label for="email">E-mail</label>
        <input
          class="form-control"
          type="text"
          placeholder=""
          name="email"
          v-model="email"
          id="email"
          >
      </div>
      <div class="form-group">
        <label for="positionName">Ime Pozicije</label>
        <input
          class="form-control"
          type="text"
          placeholder=""
          name="positionName"
          v-model="positionName"
          id="positionName"
          >
      </div>
      <div class="form-group">
        <label for="description">Opis posla</label>
        <input
          class="form-control"
          type="text"
          placeholder=""
          name="description"
          v-model="description"
          id="description"
          >
      </div>
      <div class="form-group">
        <label for="numberOfExecutors">Broj potrebnih izvršilaca</label>
        <input
          class="form-control"
          type="text"
          placeholder=""
          name="numberOfExecutors"
          v-model="numberOfExecutors"
          id="numberOfExecutors"
          >
      </div>
      <div class="form-group">
        <label for="pay">Okvirna zarada (Opciono)</label>
        <input
          class="form-control"
          type="text"
          placeholder=""
          name="pay"
          v-model="pay"
          id="pay"
          >
      </div>
      <button class="btn btn-primary" @click="submit">Posalji Formular</button>
      &nbsp;&nbsp;&nbsp;&nbsp;


</div>
</div>

</template>

<script>

// eslint-disable-next-line no-unused-vars
import {mapActions,mapGetters} from 'vuex';
export default {

  computed: mapGetters(["user"]),
  name: 'ExecutiveSearch',
  components: {

  },
  data(){
    return{
      companyName:"",
  contactName:"",
  contactNumber:"",
  email:"",
  positionName:"",
  description:"",
  numberOfExecutors:"",
    }
  },
  methods:{
  ...mapActions(['submitServiceAplication']),
  submit(){



    let text = [];
    text.push("EXECUTIVE SEARCH");
    text.push("Naziv Kompanije: "+this.companyName);
    text.push("Ime i Prezime Kontakt Osobe: "+this.contactName);
    text.push("Email: "+this.email);
    text.push("Opis Posla: "+this.description);
    text.push("Broj potrebnih izvršilaca: "+this.numberOfExecutors);
    text.push("Ime Pozicije: "+this.positionName);

    text.push("Okvirna zarada (Opciono): "+this.pay);

    let data ={
      formular:text
    }

    this.submitServiceAplication(data);
  }
  },
  beforeCreate: function() {
        document.body.className = 'usluge';
  },

}
</script>

<style lang="css" scoped>
.usluga{
  position: absolute;
  top: auto;
  left: 10%;
  right: 10%;
  background-color: white;
  border-style: solid;
  border-color: black;
  border-width: medium;
  border-radius: 15px;
  margin-top: 15px;
}
.headUsluga{
  background-color: #77bbee;
  padding: 10px;
  border-top-left-radius:  10px;
  border-top-right-radius:  10px;
}
.card-body{
  background-color: #bbffff;
  border-bottom-left-radius:  10px;
  border-bottom-right-radius:  10px;
}
</style>
